// Wave animatiom
const wave = document.getElementById('wave')

if (wave) {
	wave.animate([
		{ backgroundPosition: '0, 0' },
		{ backgroundPosition: '-9679px, 0' }
	], {
		duration: 15000,
		iterations: Infinity
	})
}


const wave2 = document.getElementById('wave-2')

if (wave2) {
	wave2.animate([
		{ backgroundPosition: '0, 0' },
		{ backgroundPosition: '-6000px, 0' }
	], {
		duration: 25000,
		iterations: Infinity
	})
}